* {
  box-sizing: border-box;
}
sup {
  vertical-align: super;
  font-size: 0.7em;
}
button {
  border: none;
  //background: linear-gradient(to bottom, #e0cc83 0%,#c4a064 100%);
  //box-shadow: inset 0px 3px 3px 0px rgba(247,243,214,1), 0px 3px 3px 3px rgba(95,70,54,1);
  height: 58px;
  padding-left: 46px;
  padding-right: 46px;
  border-radius: 5px;
  background: transparent url(../img/button-bg.png) 0 0 no-repeat;
  font-family: "PF Agora Sans Pro Medium", sans-serif;
  font-size: 25px;
  color: $black;
  transition: all ease 0.3s;
  background-size: 100% 100%;
  &:hover {
    background: url("../img/button-bg-hover.png");
    background-size: 100% 100%;
  }
}
input {
  border: 1px solid #e2e2e2;
}
.container {
  width: 1148px;
  margin: 0 auto;
}

header {
  background: #000 url(../img/header-bg.jpg) 0 0 no-repeat;
  display: flex;
  flex-direction: column;
  .header__top {
    border-bottom: 1px solid #78787c;
    height: 105px;
    width: 100%;
    display: flex;
    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .logo {
      display: flex;
      min-width: 364px;
      max-width: 364px;
      position: relative;
      justify-content: space-between;
      align-items: center;
      .logo-img {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-width: 168px;
        margin-right: 17px;
      }
      a {
        position: absolute;
        width: 100%;
        height: 100%;
      }
      span {
        color: $white;
        font-size: 24px;
        font-family: "PF Agora Slab Pro Medium", sans-serif;
      }
      span.logo-text {
        color: $gold;
      }
    }
    .adress {
      display: flex;
      flex-direction: column;
      width: 273px;
      a {
        color: $white;
        padding-left: 30px;
        position: relative;
        text-decoration: none;
      }
      .adress__local {
        margin-bottom: 17px;
        &:before {
          position: absolute;
          content: "";
          width: 20px;
          height: 26px;
          background: transparent url(../img/sprite.png) -11px 0 no-repeat;
          top: 0;
          left: 0;
        }
      }
      .adress__mail {
        &:before {
          position: absolute;
          content: "";
          width: 20px;
          height: 26px;
          background: transparent url(../img/sprite.png) -63px -4px no-repeat;
          top: 0;
          left: 0;
        }
      }
    }
    .telephone {
      width: 305px;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      &:before {
        position: absolute;
        top: 5px;
        left: -36px;
        content: '';
        width: 28px;
        height: 28px;
        background: transparent url(../img/sprite.png) -116px 0 no-repeat;
      }
      a.tel {
        font-family: "PF Agora Sans Pro Medium", sans-serif;
        font-size: 37px;
        color: $white;
        text-decoration: none;
      }
      span {
        font-size: 18px;
        color: $white;
      }
      a.toform {
        font-family: "PF Agora Sans Pro Medium", sans-serif;
        font-size: 18px;
        color: $gold;
        text-decoration: underline;
      }
    }
  }
  .header__body {
    height: 425px;
    .container {
      display: flex;
      flex-direction: column;
      height: 100%;
      align-items: center;
      justify-content: space-around;
      h1 {
        text-align: center;
        width: 100%;
      }
      a {
        font-family: "PF Agora Sans Pro Medium",sans-serif;
        font-size: 37px;
        color: $white;
        text-decoration: none;
        position: relative;
        height: 146px;
        &:hover {
          &:before {
            animation: paused;
          }
        }
        &:before {
          position: absolute;
          content: '';
          bottom: 0;
          left: 0;
          right: 0;
          margin-left: auto;
          margin-right: auto;
          width: 42px;
          height: 32px;
          background: transparent url(../img/sprite.png) -193px 0 no-repeat;
          animation: arrow 1s infinite linear alternate;
        }
      }
    }
    .header__body-warapper {
      border: 1px solid $gold;
      padding: 40px 57px;
      h3 {
        text-align: center;
        color: $gold;
        font-family: "PF Agora Sans Pro Medium", sans-serif;
      }
      .like {
        font-size: 47px;
        margin-bottom: 26px;
      }
      .like-after {
        font-size: 33px;
        margin-bottom: 56px;
      }
      ul {
        width: 602px;
        margin-left: auto;
        margin-right: auto;
        li {
          padding-left: 73px;
          padding-bottom: 24px;
          position: relative;
          &:before {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            background: url(../img/sprite.png) no-repeat;
            width: 46px;
            height: 46px;
          }
          &:nth-child(1){
            &:before {
              background-position: 0 -162px;
            }
          }
          &:nth-child(2){
            &:before {
              background-position: -91px -162px;
            }
          }
          &:nth-child(3){
            &:before {
              background-position: -182px -162px;
            }
          }
          p {
            font-family: "PF Agora Sans Pro Bold", sans-serif;
            font-size: 24px;
            color: $white;
          }
          span {
            font-family: "PF Agora Sans Pro", sans-serif;
            font-size: 24px;
            color: $white;
          }
        }
      }
    }
  }
}
.menu {
  background: #000;
  height: 68px;
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 100;
  nav {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    a.active {
      text-decoration: underline;
    }
    a {
      font-family: "PF Agora Sans Pro Medium", sans-serif;
      font-size: 24px;
      color: $white;
      text-decoration: none;
      border-right: 1px solid $white;
      padding-right: 25px;
      padding-top: 6px;
      padding-bottom: 6px;
      &:hover {
        text-decoration: underline;
      }
      &:nth-child(4) {
        padding-right: 0;
        border-right: none;
      }
    }
    button {
      height: 52px;
      padding-bottom: 6px;
    }
  }
}
.banner {
  background: transparent url(../img/banner-bg.jpg) 0 0 no-repeat;
  .container {
    background: transparent url(../img/catalog.png) 0 center no-repeat;
    height: 537px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .form__wrapper {
    width: 380px;
    height: 386px;
    margin-left: auto;
    form {
      background: #383838;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 34px 33px;
      h3 {
        margin-bottom: 26px;
      }
      input {
        height: 49px;
        padding-left: 49px;
        width: 100%;
      }
      .phone {

      }
      .email {

      }
      .input-group {
        position: relative;
        &:nth-child(2) {
          margin-bottom: 16px;
        }
        &:nth-child(3) {
          margin-bottom: 19px;
        }
      }
      label {
        position: absolute;
        content: '';
        background: #ffffff url(../img/sprite.png) no-repeat;
        width: 21px;
        height: 21px;
        top: 0;
        bottom: 0;
        margin-top: auto;
        margin-bottom: auto;
        left: 15px;
      }
      .label-phone {
        background-position: -11px -50px;
      }
      .label-email {
        background-position: -63px -50px;
      }
    }
  }
}
.edge {
  height: 184px;
  background: #ffffff;
  display: flex;
  align-items: center;
  .container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .item {
    display: flex;
    h3 {
      font-family: "PF Agora Sans Pro Medium", sans-serif;
      font-size: 43px;
      color: $black;
      text-align: center;
    }
    p {
      font-family: "PF Agora Sans Pro", sans-serif;
      font-size: 21px;
      color: $black;
      text-align: center;
    }
    .about {
      margin-left: 10px;
    }
    &:before {
      content: '';
      min-width: 118px;
      height: 118px;
      background: transparent url(../img/sprite.png) no-repeat;
    }
  }
  .item1 {
    width: 294px;
    &:before {
      background-position: 0 -247px;
    }
  }
  .item2 {
    width: 350px;
    &:before {
      background-position: -158px -247px;
    }
  }
  .item3 {
    width: 344px;
    &:before {
      background-position: -350px -247px;
    }
  }
}
.cash {
  background: #000;
  padding-top: 53px;
  padding-bottom: 70px;
  h3.title-block {
    color: $white;
    margin-bottom: 45px;
  }
  .card__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .card {
      width: 266px;
      background: #ffffff;
      padding-bottom: 23px;
      height: 496px;
      display: flex;
      flex-direction: column;
      border-radius: 3px;
      &:nth-child(1) {
        h3.title-card {
          &:before {
            background-position: 0px -417px;
          }
        }
      }
      &:nth-child(2) {
        h3.title-card {
          &:before {
            background-position: -159px -417px;
          }
        }
      }
      &:nth-child(3) {
        h3.title-card {
          &:before {
            background-position: -323px -417px;
            min-width: 50px;
          }
        }
      }
      &:nth-child(4) {
        h3.title-card {
          &:before {
            background-position: -456px -417px;
          }
        }
      }
      h3.title-card {
        font-family: "PF Agora Sans Pro", sans-serif;
        font-size: 30px;
        color: $black;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        border-bottom: 5px solid #dedede;
        padding-bottom: 9px;
        padding-left: 24px;
        padding-right: 24px;
        padding-top: 18px;
        background: #faf6ea;
        border-top-right-radius: 3px;
        border-top-left-radius: 3px;
        margin-bottom: 30px;
        min-height: 126px;
        &:before {
          content: '';
          min-width: 80px;
          min-height: 94px;
          background: url(../img/sprite.png) no-repeat;
          margin-right: 10px;
        }
        &:after {
          position: absolute;
          content: '';
          left: 0;
          bottom: -5px;
          height: 5px;
          width: 72px;
          background: $gold;
        }
      }
      ul {
        padding-left: 14px;
        padding-right: 14px;
        li {
          padding-left: 15px;
          position: relative;
          margin-bottom: 15px;
          &:last-child {
            margin-bottom: 0;
          }
          &:before {
            position: absolute;
            width: 4px;
            height: 4px;
            content: '';
            left: 0;
            top: 9px;
            border-radius: 50%;
            background: $gold;
          }
          span {
            font-size: 18px;
            color: #000;
            font-family: "PF Agora Sans Pro", sans-serif;
            i {
              font-family: "PF Agora Sans Pro Italic", sans-serif;
              font-size: 17px;
            }
          }
        }
      }
      span.sum {
        margin-top: auto;
        margin-bottom: 28px;
        text-align: center;
        font-family: "PF Agora Sans Pro Bold", sans-serif;
        font-size: 32px;
        span {
          font-size: 26px;
        }
      }
      button {
        font-size: 17px;
        padding-left: 0;
        padding-right: 0;
        margin-left: 22px;
        margin-right: 22px;
        height: 48px;
      }
    }
  }

}
.stages {
  background: url("../img/stages-bg.jpg") center no-repeat;
  padding-top: 72px;
  padding-bottom: 65px;
  h3.title-block {
    color: $white;
    margin-bottom: 46px;
  }
  ul {
    display: flex;
    justify-content: space-around;
    li {
      display: flex;
      flex-direction: column;
      width: 145px;
      align-items: center;
      position: relative;
      &:before {
        position: absolute;
        content: '';
        right: -30px;
        top: 40px;
        width: 20px;
        height: 60px;
        background: transparent url(../img/sprite.png) -277px 0 no-repeat;
      }
      &:last-child {
        &:before {
          display: none;
        }
      }
      .circle {
        width: 140px;
        height: 140px;
        border: 1px solid $gold;
        border-radius: 50%;
        position: relative;
        background: url(../img/sprite.png) no-repeat;
        span.number {
          position: absolute;
          font-family: "PF Agora Sans Pro Medium", sans-serif;
          font-size: 40px;
          color: $white;
          top: -7px;
          left: -10px;


          text-shadow:
                  -0   -4px 0   #404040,
                  0   -4px 0   #404040,
                  -0    4px 0   #404040,
                  0    4px 0   #404040,
                  -4px -0   0   #404040,
                  4px -0   0   #404040,
                  -4px  0   0   #404040,
                  4px  0   0   #404040,
                  -1px -4px 0   #404040,
                  1px -4px 0   #404040,
                  -1px  4px 0   #404040,
                  1px  4px 0   #404040,
                  -4px -1px 0   #404040,
                  4px -1px 0   #404040,
                  -4px  1px 0   #404040,
                  4px  1px 0   #404040,
                  -2px -4px 0   #404040,
                  2px -4px 0   #404040,
                  -2px  4px 0   #404040,
                  2px  4px 0   #404040,
                  -4px -2px 0   #404040,
                  4px -2px 0   #404040,
                  -4px  2px 0   #404040,
                  4px  2px 0   #404040,
                  -3px -4px 0   #404040,
                  3px -4px 0   #404040,
                  -3px  4px 0   #404040,
                  3px  4px 0   #404040,
                  -4px -3px 0   #404040,
                  4px -3px 0   #404040,
                  -4px  3px 0   #404040,
                  4px  3px 0   #404040,
                  -4px -4px 0   #404040,
                  4px -4px 0   #404040,
                  -4px  4px 0   #404040,
                  4px  4px 0   #404040,
                  -4px -4px 0   #404040,
                  4px -4px 0   #404040,
                  -4px  4px 0   #404040,
                  4px  4px 0   #404040;
        }
      }
      &:nth-child(1) { .circle {
        background-position: 33px -525px;}}
      &:nth-child(2) { .circle {
        background-position: -123px -525px;}}
      &:nth-child(3) { .circle {
        background-position: -295px -525px}}
      &:nth-child(4) { .circle {
        background-position: -446px -525px}}
      &:nth-child(5) { .circle {
        background-position: -589px -525px}}
      &:nth-child(6) { .circle {
        background-position: -723px -525px}}
    }
  }
  span.text {
    font-size: 22px;
    font-family: "PF Agora Sans Pro", sans-serif;
    color: $white;
    text-align: center;
    margin-top: 20px;
  }
}
.complete {
  background: #f6f6f6;
  padding-top: 68px;
  padding-bottom: 54px;
  h3.title-block {
    margin-bottom: 51px;
  }
  .projects__wrapper {
    display: flex;
    flex-direction: column;
    .project {
      background: $white;
      border: 1px solid #dedede;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 23px;
      &:nth-child(3) {
        margin-bottom: 57px;
      }
      .slider {
        width: 50%;
        padding: 25px 12px 25px 15px;
      }
      article {
        width: 50%;
        padding: 25px 15px 25px 12px;
        h3.article-title {
          font-family: "PF Agora Sans Pro Medium", sans-serif;
          font-size: 28px;
          margin-bottom: 30px;
          padding-bottom: 34px;
          border-bottom: 5px solid #dedede;
          position: relative;
          &:before {
            position: absolute;
            content: '';
            bottom: -5px;
            left: 0;
            height: 5px;
            width: 74px;
            background: $gold;
          }
        }
        p {
          font-family: "PF Agora Sans Pro", sans-serif;
          font-size: 19px;
          line-height: 23px;
          margin-bottom: 16px;
          b {
            font-family: "PF Agora Sans Pro Medium", sans-serif;
            font-size: 22px;
          }
        }
      }
    }
    .youto {
      background: #383838;
      padding: 42px 58px 46px;
      h3.title-form {
        font-family: "PF Agora Sans Pro Medium", sans-serif;
        font-size: 51px;
        color: $white;
        margin-bottom: 32px;
      }
      p.text-title {
        font-family: "PF Agora Sans Pro", sans-serif;
        font-size: 30px;
        color: $white;
        margin-bottom: 33px;
      }
      form {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        width: 100%;
      }
      button {
        width: 386px;
        background-size: 100% 100%;
        padding-bottom: 8px;
        font-size: 25px;
        padding-left: 0;
        padding-right: 0;
      }
      input {
        min-width: 303px;
        height: 48px;
        padding-left: 40px;
      }
      .input-group {
        position: relative;
        label {
          position: absolute;
          height: 19px;
          width: 19px;
          top: 0;
          left: 12px;
          bottom: 0;
          margin-top: auto;
          margin-bottom: auto;
          background: url(../img/sprite.png) -121px -51px no-repeat;
        }
        &:nth-child(2) {
          label {
            background-position: -11px -50px;
          }
        }
      }
    }
  }
}
.map {
  height: 581px;
  width: 100%;
  position: relative;
  z-index: 10;
  display: flex;
  align-items: center;

    .adress-form {
      padding: 40px 50px 40px 94px;
      background: #ffffff;
      opacity: 0.83;
      width: 377px;
      display: flex;
      flex-direction: column;
      position: absolute;
      left: calc((100vw - 1148px)/ 2);
      z-index: 11;
      top: 0;
      bottom: 0;
      margin-top: auto;
      margin-bottom: auto;
      height: 460px;
      li {
        padding-top: 33px;
        position: relative;
        z-index: 40;
        &:before {
          position: absolute;
          top: 30px;
          left: -71px;
          background: url(../img/sprite.png) 0 0 no-repeat;
          width: 46px;
          height: 46px;
          z-index: 40;
          content: '';
        }
        &:nth-child(1) {
          padding-top: 0;
          &:before {
            top: -6px;
            background-position: -285px -162px;
          }
        }
        &:nth-child(2) {
          &:before {
            background-position: -386px -162px;
          }
        }
        &:nth-child(3) {
          &:before {
            background-position: -492px -162px;
          }
        }
        &:nth-child(4) {
          &:before {
            background-position: -598px -162px;
          }
        }
        h3 {
          font-size: 23px;
          color: #000;
          font-family: "PF Agora Sans Pro Bold", sans-serif;
          margin-bottom: 6px;
        }
        span {
          font-size: 21px;
        }
      }
    }
}

footer {
  margin-top: 17px;
  margin-bottom: 21px;
  .container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .info {
      display: flex;
      flex-direction: column;
      max-width: 364px;
      min-width: 329px;
      p {
        font-size: 16px;
        margin-bottom: 9px;
      }
    }
    .conf {
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      a {
        font-size: 16px;
        color: #4960bf;
      }
      p {
        display: flex;
        flex-direction: column;
        font-size: 15px;
        color: $black;
      }
    }
    .tel {
      display: flex;
      flex-direction: column;
      width: 348px;
      padding-left: 35px;
      .number {
        font-family: "PF Agora Sans Pro Medium", sans-serif;
        font-size: 37px;
        text-decoration: none;
        color: $black;
        position: relative;
        word-spacing: 0;
        margin-bottom: 7px;
        &:before {
          position: absolute;
          content: '';
          top: 6px;
          left: -34px;
          width: 27px;
          height: 27px;
          background: url(../img/sprite.png) -117px 0 no-repeat;
        }
      }
      button {
        margin-bottom: 15px;
        height: 51px;
        padding-bottom: 6px;
      }
      .mail {
        text-decoration: none;
        color: $black;
        font-size: 22px;
        position: relative;
        &:before {
          position: absolute;
          content: '';
          left: -34px;
          top: 6px;
          width: 20px;
          height: 13px;
          background: url(../img/sprite.png) -63px -8px no-repeat;
        }
      }
    }
  }
}

.box-modal {
  position: relative;
  &_close {
    width: 34px;
    height: 34px;
    position: absolute;
    top: -21px;
    right: -54px;
    background: url(../img/sprite.png) -5px -99px no-repeat;
    cursor: pointer;
  }
  &__wrapper {
    width: 431px;
    padding: 32px 32px;
    background: $white;
    display: flex;
    flex-direction: column;
    h3 {
      text-align: center;
      font-size: 27px;
      color: $modal-text;
      text-transform: uppercase;
      font-family: "PF Agora Sans Pro Bold", sans-serif;
      margin-bottom: 20px;
    }
    p {
      text-align: center;
      font-size: 22px;
      color: $modal-text;
      margin-bottom: 23px;
    }
    form {
      display: flex;
      flex-direction: column;
      input {
        width: 100%;
        height: 49px;
        padding-left: 47px;
      }
      .input-group {
        position: relative;
        margin-bottom: 16px;
        label {
          position: absolute;
          height: 19px;
          width: 19px;
          top: 0;
          left: 12px;
          bottom: 0;
          margin-top: auto;
          margin-bottom: auto;
          background: url(../img/sprite.png) -121px -51px no-repeat;
        }
        &:nth-child(2) {
          label {
            background-position: -11px -50px;
          }
        }
      }
      button {
        font-size: 24px;
        padding-left: 0;
        padding-right: 0;
      }
    }
  }
}


