.tumbs {
  width: 483px;
  margin-left: auto;
  margin-right: auto;
  height: 110px;
  .slides {
    height: 100%;
    li {
      border: 5px solid #d2dce4;
      height: 110px;
      overflow: hidden;
      img {
        height: 100%;
        width: 100%;
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .flex-direction-nav {
    .flex-prev {
      height: 57px;
      width: 23px;
      background: url(../img/sprite.png) -14px -674px no-repeat;
      opacity: 0.7;
      left: -30px !important;
      top: 0;
      bottom: 0;
      margin-top: auto;
      margin-bottom: auto;
      &:hover {
        left: -30px;
      }
      &:before {
        display: none;
      }
    }
    .flex-next {
      height: 57px;
      width: 23px;
      right: -30px !important;
      opacity: 0.7;
      top: 0;
      bottom: 0;
      margin-top: auto;
      margin-bottom: auto;
      &:hover {
        right: -25px;
      }
      &:before {
        display: none;
      }
      background: url(../img/sprite.png) -180px -674px no-repeat;
    }
  }
}

